.contact-container {
  width: 100%;
  height: 100vh;

  .contact-title {
    width: 100%;

    & > h1 {
      font-size: $x-large;
      font-family: $unbounded;
      text-transform: uppercase;
      margin: 10rem 0;
    }
  }

  .contact-list {
    display: flex;
    margin: 0 5%;
    flex-flow: column;

    & > div {
      border-bottom: 5px solid $blk;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 0.5rem 0;
      padding: 0 1rem;

      &:nth-child(1) {
        border-top: 5px solid $blk;
      }

      & > a {
        color: $blk;

        &:after {
          content: "";
          position: relative;
          display: block;
          width: 0px;
          height: 5px;
          margin: 1rem 0 0 0;
          transition: all 0.2s ease;
          transition-duration: 0.3s;
          opacity: 0;
          background-color: $blk;
        }
        &:hover::after {
          width: 100%;
          opacity: 1;
        }
      }
    }
  }
}
