html,
body {
  margin: 0;
  padding: 0;
  font-family: $questrial;
  background-color: $lght-gry;
  scroll-behavior: smooth;
  scrollbar-width: none;
  overflow-x: hidden;

  ::selection {
    color: $lght-gry;
    background: $blk;
  }

  section {
    width: 100%;
  }

  a {
    color: $blk;
    text-decoration: none;
  }
}
