.mobile-menu {
  position: relative;
  display: none;

  button {
    position: fixed;
    left: 1rem;
    top: 1rem;
    z-index: 10;
    cursor: pointer;
    border: none;

    .navbar-icon {
      width: 3rem;
      height: 3rem;
      color: $blk;
    }
  }
  .menuNav {
    margin: 0;
    padding: 0;
    overflow-y: hidden;
    list-style: none;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
    top: 0;
    left: 0;
    bottom: 0;
    width: 0;
    background: $blk;
    height: 100vh;
    overflow: hidden;
    max-width: 100vw;
    z-index: 9;
    transition: 0.5s width cubic-bezier(0.25, 0.6, 0.45, 0.94);

    &.showMenu {
      padding: 0;
      width: 100%;
      transition: 0.5s width cubic-bezier(0.25, 0.6, 0.45, 0.94);
    }
    li {
      text-transform: uppercase;
      color: $blk;
      padding: 2rem 0;
      font-size: $large;

      &:after {
        content: "";
        position: relative;
        display: block;
        width: 0px;
        height: 5px;
        margin: 1rem 0 0 0;
        transition: all 0.2s ease;
        opacity: 0;
        background-color: $blk;
        z-index: 10;
      }
      &:hover::after {
        width: 75%;
        opacity: 1;
      }

      &:first-child {
        margin-top: 7rem;
      }
    }
  }
}
.lang-select {
  text-transform: uppercase;
  padding: 1rem;
  font-family: $questrial;
  cursor: pointer;
}

.nav-main {
  display: none;
}
.mobile-menu {
  width: 100%;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;

  .menu-toggle-btn {
    font-size: $large;
    background: none;

    & > p {
      padding: 0;
      margin: 0;
    }
  }
}
